/* src/components/BottomBar.css */


.bottom-bar {
    background-color: #f2f2f2; /* Set background color as needed */
    padding: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    bottom: 0;
    width: 100%;
  }
  
  .bottom-links {
    display: flex;
    gap: 15px;
  }
  
  .settings-link {
    margin-left: auto; /* Push the settings link to the right side */
    padding-right: 20px
  }
  
  a {
    text-decoration: none;
    color: #1a0dab; /* Set link color as needed */
  }
  