/* src/components/NavBar.css */

.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #4285f4; /* Set background color as needed */
    padding: 10px 20px;
    color: white; /* Set text color as needed */
  }
  
  .left-links, .right-links {
    display: flex;
    gap: 15px;
  }
  
  .right-links {
    margin-left: auto; /* Push the right links to the right side */
  }
  
  a {
    color: white;
    text-decoration: none;
  }